import React from 'react';
import { Link } from 'react-router-dom';
import { Dropdown, Menu } from 'semantic-ui-react'

 
const Header = () => {
  return(
    <Menu pointing secondary>
      <Link to ='/' className="item">
        Home
      </Link>
      <Dropdown item text="Notes">
        <Dropdown.Menu>
          <Dropdown item text='Teaching'>
              <Dropdown.Menu>
                  <Dropdown.Item >
                      <Link to ='/rn' className="item">
                       Interior point method example
                      </Link>                
                  </Dropdown.Item>
              </Dropdown.Menu>  
          </Dropdown >
        </Dropdown.Menu>
      </Dropdown>  
    </Menu>
 )
}
export default Header;